@import url('https://fonts.googleapis.com/css2?family=Hind&display=swap');

body {
  background: #e8eaf1;
  font-family: 'Hind', Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.app {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
}

textarea {
  background: #eff1f5;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 20px;
  font-size: 16px;
}

.preview {
  padding-left: 20px;
  padding-right: 20px;
}

blockquote {
  font-size: 20px;
  background: #efefef;
  border-left: 12px solid rgb(255, 2, 200);
  padding: 10px 20px;
  border-radius: 5px;
  margin: 0;
}

footer {
  font: "Helvetica", sans-serif;
  font-weight: bold;
  text-align: center;
  border-style: dashed;
  border-width: 1px;
  position: absolute;
  bottom: 0;
  right: 0;
}